import React, { useState, useEffect, useRef } from "react"; // Import React hooks
import styled, { keyframes } from "styled-components"; // Import keyframes along with styled-components
import axios from "axios"; // Import Axios for HTTP requests
import ReactDOM from "react-dom"; // Import ReactDOM for rendering

const dots = keyframes`
  0% { transform: translateY(0); }
  20% { transform: translateY(-10px); }
  40% { transform: translateY(0); }
`;
const LoadingDots = styled.div`
  display: flex;
  justify-content: start;
  align-items: center;
  height: 20px;
  margin-top: 10px;
`;

const Dot = styled.div`
  width: 8px;
  height: 8px;
  background-color: #333;
  border-radius: 50%;
  margin: 0 3px;
  animation: ${dots} 0.8s infinite;
  animation-delay: ${props => props.delay || "0s"};
`;
const ChatbotContainer = styled.div`
  position: fixed;
  bottom: 20px;
  right: 20px;
  width: 350px;
  max-height: 700px;
  background-color: #f4f7fb; /* Light background */
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  font-family: "Arial, sans-serif";
`;

const ChatbotHeader = styled.div`
  background-color: #0a1128; /* Dark background */
  color: #fff;
  padding: 20px;
  font-size: 16px;
  font-weight: bold;
  display: flex;
  justify-content: space-between; /* Added to space between title and close button */
  align-items: center;
`;

const CloseButton = styled.button`
  background-color: transparent;
  border: none;
  color: #fff;
  font-size: 20px;
  cursor: pointer;
  margin-left: auto; /* Align close button to the right */
`;

const ChatbotBody = styled.div`
  display: ${(props) => (props.isOpen ? "block" : "none")};
  padding: 10px;
  max-height: 400px;
  overflow-y: auto;
`;

const BotAvatar = styled.div`
  display: flex;
  justify-content: center;
  margin: 20px 0;

  img {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    background-color: #ddd; /* Placeholder avatar background */
  }
`;

const BotDescription = styled.p`
  text-align: center;
  color: #666;
  font-size: 14px;
  margin-bottom: 20px;
`;

const InputArea = styled.div`
  display: flex;
  border-top: 1px solid #eee;
  padding: 10px;
`;

const Input = styled.input`
  flex: 1;
  border: 1px solid #ddd;
  border-radius: 5px;
  padding: 10px;
  font-size: 14px;
`;

const SendButton = styled.button`
  background-color: #0a1128;
  color: #fff;
  border: none;
  border-radius: 5px;
  padding: 0 15px;
  cursor: pointer;
  margin-left: 10px;
`;

const ToggleButton = styled.button`
  position: fixed;
  bottom: 20px;
  right: 20px;
  background-color: #0a1128;
  border: none;
  color: #fff;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  font-size: 30px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
`;

// Define Messages and Message components
const Messages = styled.div`
  margin-bottom: 10px;
  max-height: 300px;
  overflow-y: auto;
`;

const Message = styled.div`
  margin: 5px 0;
  padding: 10px;
  background-color: ${(props) => (props.isUser ? "black" : "#E5E5EA")};
  color: ${(props) => (props.isUser ? "white" : "#000")};
  border-radius: 10px;
  text-align: ${(props) => (props.isUser ? "right" : "left")};
  max-width: 80%;
  align-self: ${(props) => (props.isUser ? "flex-end" : "flex-start")};
`;

function Chatbot(props) {
  const [isOpen, setIsOpen] = useState(false);
  const [loading, setLoading] = useState(false); // State for loading animation

  const [messages, setMessages] = useState([
    {
      isUser: false,
      text: props.welcomeMessage || "Hello! How can I assist you today?",
    },
  ]);
  const [inputText, setInputText] = useState("");
  const messagesEndRef = useRef(null);
  const [sessionId, setSessionId] = useState("");
  const [chatbotId, setchatbotId] = useState("");
  // Scroll to bottom when messages update
  useEffect(() => {
    if (messagesEndRef.current) {
      messagesEndRef.current.scrollIntoView({ behavior: "smooth" });
    }
  }, [messages]);

  const handleToggle = () => {
    const chatbot_id = window.chatbotConfig?.chatbotId;
  console.log(window.chatbotConfig);
  
    if (!chatbot_id) {
      console.error("Chatbot ID is not available in chatbotConfig.");
      return;
    }
  
    console.log("Chatbot ID found:", chatbot_id);
  
    axios
      .post(`http://127.0.0.1:8000/api/v1/chatbots/${chatbot_id}/sessions`, {})
      .then((response) => {
        console.log("Session created:", response.data);
        setSessionId(response.data.session_id);
        setchatbotId(response.data.chatbot_id);
      })
      .catch((error) => {
        console.error("Error sending message:", error);
      });
  
    // Toggle the chatbot visibility
    setIsOpen(!isOpen);
  };
  
  const handleClose = () => {
    setIsOpen(false); // Close the chatbot when the close button is clicked
  };

  const handleSend = () => {
    if (inputText.trim() === "") return;

     const newMessages = [...messages, { isUser: true, text: inputText }];
    setMessages(newMessages);
    setInputText("");
    setLoading(true); 
     axios
      .post(`http://127.0.0.1:8000/api/v1/chatbots/${chatbotId}/sessions/${sessionId}/messages`, { message: inputText })
      .then((response) => {
         setMessages((prevMessages) => [
          ...prevMessages,
          { isUser: false, text: response.data.chatbot_response },
        ]);
        setLoading(false);  // Stop the loading animation
      })
      .catch((error) => {
        console.error("Error sending message:", error);
        setMessages((prevMessages) => [
          ...prevMessages,
          { isUser: false, text: "Sorry, an error occurred." },
        ]);
      });
       
  };

  const handleInputKeyPress = (e) => {
    if (e.key === "Enter") {
      handleSend();
    }
  };

  return (
    <>
      {isOpen && (
        <ChatbotContainer>
          <ChatbotHeader>
            Chat with Customer Support Bot
            <CloseButton onClick={handleClose}>&times;</CloseButton>{" "}
           </ChatbotHeader>
          <ChatbotBody isOpen={isOpen}>
            <BotAvatar>
              <div
                style={{
                  backgroundColor: "#D1D5DB",
                  borderRadius: "9999px",
                  width: "4rem",
                  height: "4rem",
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "center",
                  marginBottom: "1rem",
                }}
              >
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  viewBox="0 0 24 24"
                  fill="gray"
                  width="48px"
                  height="48px"
                >
                  <path d="M12 12c2.21 0 4-1.79 4-4s-1.79-4-4-4-4 1.79-4 4 1.79 4 4 4zm0 2c-2.67 0-8 1.34-8 4v2h16v-2c0-2.66-5.33-4-8-4z" />
                </svg>{" "}
              </div>{" "}
            </BotAvatar>
            <BotDescription>
              A smart chatbot, always ready to solve customer inquiries. Natural
              conversation accurate responses tailored to your needs.
            </BotDescription>
            <Messages>
              {messages.map((msg, index) => (
                <Message key={index} isUser={msg.isUser}>
                  {msg.text}
                </Message>
              ))}
               {loading && (
                <LoadingDots>
                  <Dot delay="0s" />
                  <Dot delay="0.1s" />
                  <Dot delay="0.2s" />
                </LoadingDots>
              )}
              <div ref={messagesEndRef} />
            </Messages>
          </ChatbotBody>
          <InputArea>
            <Input
              type="text"
              placeholder="Say hi"
              value={inputText}
              onChange={(e) => setInputText(e.target.value)}
              onKeyPress={handleInputKeyPress}
            />
            <SendButton onClick={handleSend}></SendButton>
          </InputArea>
        </ChatbotContainer>
      )}
      {!isOpen && <ToggleButton onClick={handleToggle}></ToggleButton>}
    </>
  );
}

// Export the init function as default export
export default function init(config = {}) {
  const container = document.createElement("div");
  container.id = "chatbot-root";
  document.body.appendChild(container);

  ReactDOM.render(<Chatbot {...config} />, container);
}
